import React from 'preact/compat';

interface SkeletonLoaderProps {
  message?: string;
  showDays?: boolean;
  itemCount?: number;
}

export function SkeletonLoader({ message = "Loading...", showDays = false, itemCount = 5 }: SkeletonLoaderProps) {
  return (
    <div class="animate-pulse">
      {/* Header skeleton if needed */}
      {showDays && (
        <>
          <div class="h-8 bg-gray-700 rounded mb-6 w-1/3"></div>
          
          {/* Day group skeleton */}
          <div class="mb-8">
            <div class="flex items-center justify-between mb-4">
              <div class="h-6 bg-gray-700 rounded w-32"></div>
              <div class="h-6 bg-gray-700 rounded w-16"></div>
            </div>
            
            {/* Event cards skeleton */}
            <div class="space-y-2">
              {Array.from({ length: itemCount }, (_, i) => (
                <SkeletonEventCard key={i} />
              ))}
            </div>
          </div>
        </>
      )}
      
      {!showDays && (
        <div class="space-y-2">
          {Array.from({ length: itemCount }, (_, i) => (
            <SkeletonEventCard key={i} />
          ))}
        </div>
      )}
      
      {/* Loading message */}
      <div class="text-center mt-8">
        <div class="h-4 bg-gray-700 rounded w-48 mx-auto mb-2"></div>
        <p class="text-gray-400">{message}</p>
      </div>
    </div>
  );
}

function SkeletonEventCard() {
  return (
    <div class="card mb-2 md:mb-4">
      <div class="flex justify-between items-start">
        <div class="flex-1">
          {/* Band name */}
          <div class="h-6 bg-gray-700 rounded w-3/4 mb-2"></div>
          {/* Genre */}
          <div class="h-4 bg-gray-700 rounded w-1/2 mb-3"></div>
        </div>
        {/* Favorite button */}
        <div class="h-6 w-6 bg-gray-700 rounded"></div>
      </div>
      
      {/* Event details */}
      <div class="space-y-2 mt-3">
        {/* Date */}
        <div class="flex items-center">
          <div class="w-20 h-4 bg-gray-700 rounded mr-4"></div>
          <div class="h-4 bg-gray-700 rounded w-24"></div>
        </div>
        {/* Time */}
        <div class="flex items-center">
          <div class="w-20 h-4 bg-gray-700 rounded mr-4"></div>
          <div class="h-4 bg-gray-700 rounded w-32"></div>
        </div>
        {/* Venue */}
        <div class="flex items-center">
          <div class="w-20 h-4 bg-gray-700 rounded mr-4"></div>
          <div class="h-4 bg-gray-700 rounded w-40"></div>
        </div>
        {/* Address */}
        <div class="ml-20">
          <div class="h-3 bg-gray-700 rounded w-56"></div>
        </div>
      </div>
    </div>
  );
}

export function SkeletonApp() {
  return (
    <div class="min-h-screen flex flex-col bg-gray-900 text-white">
      {/* Header skeleton */}
      <header class="bg-gray-800 shadow-md sticky top-0 z-50">
        <div class="container mx-auto px-4 py-4">
          <div class="flex flex-col md:flex-row md:justify-between md:items-center">
            <div class="flex items-center mb-4 md:mb-0">
              <div class="flex items-center">
                <div class="hidden md:block w-8 h-8 bg-gray-700 rounded mr-3"></div>
                <div class="h-8 bg-gray-700 rounded w-32"></div>
              </div>
            </div>
            
            <div class="flex items-center space-x-6">
              {/* Navigation skeleton */}
              <nav>
                <ul class="flex space-x-6">
                  <li><div class="h-5 bg-gray-700 rounded w-12"></div></li>
                  <li><div class="h-5 bg-gray-700 rounded w-16"></div></li>
                  <li><div class="h-5 bg-gray-700 rounded w-16"></div></li>
                </ul>
              </nav>
              
              {/* Update button skeleton */}
              <div class="w-9 h-9 bg-gray-700 rounded-md"></div>
            </div>
          </div>
        </div>
      </header>
      
      {/* Main content skeleton */}
      <main class="flex-grow container mx-auto px-4 py-8">
        <SkeletonLoader message="Loading WGT web app..." showDays={false} itemCount={3} />
      </main>
      
      {/* Footer skeleton */}
      <footer class="bg-gray-800 text-gray-400 py-6 mt-auto">
        <div class="container mx-auto px-4 text-center">
          <div class="h-4 bg-gray-700 rounded w-64 mx-auto mb-2"></div>
          <div class="h-4 bg-gray-700 rounded w-48 mx-auto"></div>
        </div>
      </footer>
    </div>
  );
} 