import React, { useState, useEffect } from 'preact/compat';
import { Schedule, EventWithDetails } from '../models/types';
import { loadScheduleData, isScheduleLoaded } from '../utils/apiUtils';
import { getFavoriteEventsWithDetails, sortEventsByDateTime, generateShareableLink, decodeSharedSchedule, addFavorite, saveFavorites, loadFavorites, getAllEventsWithDetails } from '../utils/dataUtils';
import { groupEventsByDate } from '../utils/scheduleUtils';
import { EventCard } from '../components/EventCard';
import { DayGroup } from '../components/DayGroup';
import { EventFilter, FilterOptions } from '../components/EventFilter';
import { SkeletonLoader } from '../components/SkeletonLoader';

export function Favorites() {
  const [schedule, setSchedule] = useState<Schedule | null>(null);
  const [favoriteEvents, setFavoriteEvents] = useState<EventWithDetails[]>([]);
  const [filteredEvents, setFilteredEvents] = useState<EventWithDetails[]>([]);
  const [loading, setLoading] = useState(!isScheduleLoaded());
  const [error, setError] = useState<string | null>(null);
  const [uniqueDates, setUniqueDates] = useState<string[]>([]);
  const [shareUrl, setShareUrl] = useState<string>('');
  const [showShareLink, setShowShareLink] = useState(false);
  const [copied, setCopied] = useState(false);
  const [showResetConfirm, setShowResetConfirm] = useState(false);
  const [expandedDays, setExpandedDays] = useState<Record<string, boolean>>({});
  
  // Import functionality state
  const [showImportSection, setShowImportSection] = useState(false);
  const [importText, setImportText] = useState('');
  const [importError, setImportError] = useState<string | null>(null);
  const [importSuccess, setImportSuccess] = useState<string | null>(null);
  const [mergeMode, setMergeMode] = useState(true); // true = merge, false = replace

  // Filter state
  const [currentFilters, setCurrentFilters] = useState<FilterOptions>({
    date: '',
    locationId: '',
    searchTerm: '',
    category: ''
  });
  
  useEffect(() => {
    const processData = (data: Schedule) => {
        // Get favorite events with details
        const favorites = getFavoriteEventsWithDetails(data);

        // Sort events by date and time
        const sortedEvents = sortEventsByDateTime(favorites);
        
        setFavoriteEvents(sortedEvents);
        
        // Extract unique dates for the filter
        const dates = Array.from(new Set(sortedEvents.map(event => event.date)));
        setUniqueDates(dates);
    }

    async function fetchData() {
      try {
        const data = await loadScheduleData();
        setSchedule(data);
        processData(data);

      } catch (err) {
        setError('Failed to load favorites data. Please try again later.');
        console.error(err);
      } finally {
        setLoading(false);
      }
    }
    
    fetchData();
    
    // Add event listener to update favorites when they change
    const handleFavoritesChange = () => {
      if (schedule) {
        processData(schedule);
      }
    };
    
    window.addEventListener('favoritesChanged', handleFavoritesChange);
    // Keep storage event for cross-tab synchronization
    window.addEventListener('storage', fetchData);
    
    return () => {
      window.removeEventListener('favoritesChanged', handleFavoritesChange);
      window.removeEventListener('storage', fetchData);
    };
  }, [schedule]); // Remove currentFilters from dependency

  // Apply filters whenever favoriteEvents or currentFilters change
  useEffect(() => {
    if (favoriteEvents.length > 0) {
      applyFilters(favoriteEvents, currentFilters);
    } else {
      // If no favorite events, set filtered events to empty array
      setFilteredEvents([]);
    }
  }, [favoriteEvents, currentFilters]);
  
  const applyFilters = (eventsToFilter: EventWithDetails[], filters: FilterOptions) => {
    let filtered = [...eventsToFilter];
    
    // Filter by date
    if (filters.date) {
      filtered = filtered.filter(event => event.date === filters.date);
    }
    
    // Filter by location
    if (filters.locationId) {
      filtered = filtered.filter(event => event.locationId === filters.locationId);
    }
    
    // Filter by category
    if (filters.category) {
      filtered = filtered.filter(event => event.category === filters.category);
    }
    
    // Filter by search term (band name)
    if (filters.searchTerm) {
      const searchLower = filters.searchTerm.toLowerCase();
      filtered = filtered.filter(event => 
        event.band.name.toLowerCase().includes(searchLower) ||
        (event.band.genre && event.band.genre.toLowerCase().includes(searchLower))
      );
    }
    
    setFilteredEvents(filtered);
  };
  
  const handleFilterChange = (filters: FilterOptions) => {
    setCurrentFilters(filters);
    applyFilters(favoriteEvents, filters);
  };
  
  const handleShareSchedule = () => {
    const eventIds = favoriteEvents.map(event => event.id);
    const url = generateShareableLink(eventIds);
    setShareUrl(url);
    setShowShareLink(true);
    setCopied(false);
  };
  
  const handleCopyLink = () => {
    navigator.clipboard.writeText(shareUrl).then(() => {
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    });
  };

  const handleResetData = () => {
    setShowResetConfirm(true);
  };

  const confirmResetData = () => {
    localStorage.clear();
    window.location.reload();
  };

  const cancelResetData = () => {
    setShowResetConfirm(false);
  };

  // Import functionality handlers
  const handleImportSchedule = () => {
    setImportError(null);
    setImportSuccess(null);
    
    if (!importText.trim()) {
      setImportError('Please enter a shared schedule URL or data');
      return;
    }

    try {
      let eventIds: string[] = [];
      
      // Check if it's a URL
      if (importText.includes('/shared?schedule=') || importText.includes('schedule=')) {
        // Extract the schedule parameter from URL
        const url = new URL(importText.includes('http') ? importText : `${window.location.origin}${importText}`);
        const scheduleParam = url.searchParams.get('schedule');
        if (!scheduleParam) {
          throw new Error('Invalid shared schedule URL');
        }
        eventIds = decodeSharedSchedule(scheduleParam);
      } else {
        // Try to parse as direct encoded data
        eventIds = decodeSharedSchedule(importText);
        
        if (eventIds.length === 0) {
          // Try to parse as JSON array of event IDs
          const parsed = JSON.parse(importText);
          if (Array.isArray(parsed)) {
            eventIds = parsed;
          } else if (parsed.events && Array.isArray(parsed.events)) {
            // Handle favorites format
            eventIds = parsed.events;
          } else {
            throw new Error('Invalid data format. Expected an array of event IDs or an object with an "events" property containing an array.');
          }
        }
      }

      if (eventIds.length === 0) {
        throw new Error('No valid events found in the data');
      }

      console.log('Imported event IDs:', eventIds);

      // Validate that the events exist in the current schedule
      if (schedule) {
        const allEvents = getAllEventsWithDetails(schedule);
        const allEventIds = allEvents.map(event => event.id);
        
        console.log('Total events in schedule:', allEvents.length);
        console.log('Sample event IDs from schedule:', allEventIds.slice(0, 5));
        console.log('Imported event IDs:', eventIds.slice(0, 5));
        
        const validEventIds = eventIds.filter(id => 
          allEventIds.includes(id)
        );
        
        console.log('Valid event IDs found:', validEventIds.length);
        
        if (validEventIds.length === 0) {
          // Provide more detailed error information
          const sampleImported = eventIds.slice(0, 3);
          const sampleSchedule = allEventIds.slice(0, 3);
          throw new Error(
            `None of the events in the shared data exist in the current schedule.\n\n` +
            `Sample imported IDs: ${sampleImported.join(', ')}\n` +
            `Sample schedule IDs: ${sampleSchedule.join(', ')}\n\n` +
            `This usually means the data is from a different version of the schedule or contains invalid event IDs.`
          );
        }

        // Import the events
        if (mergeMode) {
          // Merge with existing favorites
          validEventIds.forEach(eventId => {
            addFavorite(eventId);
          });
          setImportSuccess(`Successfully imported ${validEventIds.length} events and merged with existing favorites`);
        } else {
          // Replace existing favorites
          const currentFavorites = loadFavorites();
          currentFavorites.events = validEventIds;
          saveFavorites(currentFavorites);
          setImportSuccess(`Successfully replaced favorites with ${validEventIds.length} events`);
        }

        if (validEventIds.length < eventIds.length) {
          setImportError(`Note: ${eventIds.length - validEventIds.length} events were not found in the current schedule and were skipped.`);
        }

        // Refresh the favorites display
        const updatedFavorites = getFavoriteEventsWithDetails(schedule);
        const sortedFavorites = sortEventsByDateTime(updatedFavorites);
        setFavoriteEvents(sortedFavorites);
        setFilteredEvents(sortedFavorites);

        // Clear the import text
        setImportText('');
      } else {
        throw new Error('Schedule data not loaded');
      }
    } catch (error) {
      console.error('Import error:', error);
      setImportError(error instanceof Error ? error.message : 'Failed to import schedule data');
    }
  };

  const handleFileImport = (event: Event) => {
    const input = event.target as HTMLInputElement;
    const file = input.files?.[0];
    
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      const content = e.target?.result as string;
      setImportText(content);
      setImportError(null);
      setImportSuccess(null);
    };
    reader.onerror = () => {
      setImportError('Failed to read file');
    };
    reader.readAsText(file);
  };

  const handleExportFavorites = () => {
    const favorites = loadFavorites();
    const dataStr = JSON.stringify(favorites, null, 2);
    const dataBlob = new Blob([dataStr], { type: 'application/json' });
    
    const url = URL.createObjectURL(dataBlob);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'wgt-favorites-backup.json';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  const toggleImportSection = () => {
    setShowImportSection(!showImportSection);
    setImportError(null);
    setImportSuccess(null);
    setImportText('');
  };
  
  const toggleDayExpanded = (date: string) => {
    setExpandedDays(prev => ({
      ...prev,
      [date]: prev[date] === undefined ? false : !prev[date]
    }));
  };

  const isDayExpanded = (date: string) => {
    return expandedDays[date] !== false; // Default to expanded
  };
  
  if (loading) {
    return <SkeletonLoader message="Loading your favorites..." showDays={false} itemCount={3} />;
  }
  
  if (error) {
    return (
      <div class="bg-red-900 text-white p-4 rounded-lg">
        <h2 class="text-xl font-bold mb-2">Error</h2>
        <p>{error}</p>
      </div>
    );
  }
  
  return (
    <div>
      <div class="flex flex-col md:flex-row md:justify-between md:items-center mb-6">
        <h1 class="text-3xl font-bold mb-4 md:mb-0">My Festival Schedule</h1>
        
        <div class="flex flex-col sm:flex-row gap-2">
          <button 
            onClick={toggleImportSection}
            class="btn btn-secondary"
          >
            {showImportSection ? 'Hide Import' : 'Import Schedule'}
          </button>
          
          {favoriteEvents.length > 0 && (
            <>
              <button 
                onClick={handleExportFavorites}
                class="btn btn-secondary"
              >
                Export Backup
              </button>
              <button 
                onClick={handleShareSchedule}
                class="btn btn-primary"
              >
                Share My Schedule
              </button>
            </>
          )}
        </div>
      </div>
      
      {/* Import Section */}
      {showImportSection && (
        <div class="bg-gray-800 p-4 rounded-lg mb-6">
          <h3 class="text-lg font-semibold mb-4">Import Schedule Data</h3>
          
          <div class="space-y-4">
            {/* Import mode toggle */}
            <div class="flex items-center space-x-4">
              <label class="text-sm font-medium">Import Mode:</label>
              <label class="flex items-center">
                <input
                  type="radio"
                  name="importMode"
                  checked={mergeMode}
                  onChange={() => setMergeMode(true)}
                  class="mr-2"
                />
                Merge with existing favorites
              </label>
              <label class="flex items-center">
                <input
                  type="radio"
                  name="importMode"
                  checked={!mergeMode}
                  onChange={() => setMergeMode(false)}
                  class="mr-2"
                />
                Replace all favorites
              </label>
            </div>

            {/* Text input for URLs or data */}
            <div>
              <label class="block text-sm font-medium mb-2">
                Paste shared schedule URL or encoded data:
              </label>
              <textarea
                value={importText}
                onChange={(e) => setImportText((e.target as HTMLTextAreaElement).value)}
                placeholder="Paste a shared schedule URL (like: https://yoursite.com/shared?schedule=...) or encoded data here"
                class="w-full bg-gray-700 text-white rounded p-3 border border-gray-600 min-h-[100px] resize-y"
              />
            </div>

            {/* File input */}
            <div>
              <label class="block text-sm font-medium mb-2">
                Or select a backup file:
              </label>
              <input
                type="file"
                accept=".json,.txt"
                onChange={handleFileImport}
                class="block w-full text-sm text-gray-400 file:mr-4 file:py-2 file:px-4 file:rounded file:border-0 file:text-sm file:font-semibold file:bg-blue-600 file:text-white hover:file:bg-blue-700"
              />
            </div>

            {/* Action buttons */}
            <div class="flex gap-2">
              <button
                onClick={handleImportSchedule}
                disabled={!importText.trim()}
                class="btn btn-primary disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Import Schedule
              </button>
              <button
                onClick={() => setImportText('')}
                class="btn btn-secondary"
              >
                Clear
              </button>
            </div>

            {/* Error/Success messages */}
            {importError && (
              <div class="bg-red-900 text-red-200 p-3 rounded">
                <pre class="whitespace-pre-wrap text-sm">{importError}</pre>
              </div>
            )}
            {importSuccess && (
              <div class="bg-green-900 text-green-200 p-3 rounded">
                {importSuccess}
              </div>
            )}
          </div>
        </div>
      )}
      
      {showShareLink && (
        <div class="bg-gray-800 p-4 rounded-lg mb-6">
          <h3 class="text-lg font-semibold mb-2">Share your schedule with friends</h3>
          <div class="flex flex-col sm:flex-row gap-2">
            <input 
              type="text" 
              value={shareUrl} 
              readonly
              class="flex-grow bg-gray-700 text-white rounded p-2 border border-gray-600"
            />
            <button 
              onClick={handleCopyLink}
              class="btn btn-secondary"
            >
              {copied ? 'Copied!' : 'Copy Link'}
            </button>
          </div>
        </div>
      )}
      
      {schedule && favoriteEvents.length > 0 && (
        <div class="sticky top-[100px] md:top-0 z-40 bg-gray-900 pb-2 -mx-4 px-4 sm:-mx-6 sm:px-6 md:-mx-8 md:px-8">
          <EventFilter 
            onFilterChange={handleFilterChange} 
            locations={schedule.locations}
            dates={uniqueDates}
            initialFilters={currentFilters}
          />
        </div>
      )}
      
      {filteredEvents.length > 0 ? (
        <div>
          {Object.entries(groupEventsByDate(filteredEvents))
            .sort(([a], [b]) => a.localeCompare(b))
            .map(([date, dayEvents]) => (
              <DayGroup
                key={date}
                date={date}
                events={dayEvents}
                isExpanded={isDayExpanded(date)}
                onToggleExpanded={toggleDayExpanded}
              />
            ))}
        </div>
      ) : (
        <div class="card text-center py-8">
          <p class="text-xl text-gray-400">
            {favoriteEvents.length > 0 
              ? 'No favorites match your filter criteria.' 
              : 'You haven\'t added any favorites yet.'}
          </p>
          <p class="text-gray-500 mt-2">
            {favoriteEvents.length > 0 
              ? 'Try adjusting your filter criteria.' 
              : 'Go to the Schedule page and click the star icon to add bands to your favorites.'}
          </p>
        </div>
      )}

      {/* Reset Data Section */}
      <div class="mt-12 pt-8 border-t border-gray-700">
        <div class="text-center">
          <h3 class="text-lg font-semibold mb-4 text-gray-300">Danger Zone</h3>
          <button 
            onClick={handleResetData}
            class="bg-red-600 hover:bg-red-700 text-white font-bold py-3 px-6 rounded-lg transition-colors duration-200"
          >
            Reset All Data
          </button>
          <p class="text-sm text-gray-400 mt-2">
            This will clear all your favorites and cached data
          </p>
        </div>
      </div>

      {/* Reset Confirmation Dialog */}
      {showResetConfirm && (
        <div class="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div class="bg-gray-800 p-6 rounded-lg max-w-md mx-4">
            <h3 class="text-xl font-bold mb-4 text-red-400">Confirm Reset</h3>
            <p class="text-gray-300 mb-6">
              Are you sure you want to reset all data? This will:
            </p>
            <ul class="list-disc list-inside text-gray-400 mb-6 space-y-1">
              <li>Clear all your favorite bands</li>
              <li>Remove cached schedule data</li>
              <li>Reset the app to its initial state</li>
            </ul>
            <p class="text-red-300 font-semibold mb-6">
              This action cannot be undone!
            </p>
            <div class="flex space-x-4">
              <button 
                onClick={cancelResetData}
                class="flex-1 bg-gray-600 hover:bg-gray-700 text-white py-2 px-4 rounded transition-colors"
              >
                Cancel
              </button>
              <button 
                onClick={confirmResetData}
                class="flex-1 bg-red-600 hover:bg-red-700 text-white py-2 px-4 rounded transition-colors"
              >
                Reset Data
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}